.singleEvent{
    height: 100%;
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}

.dark-singleEvent *{
    color: rgba(255,255,255,1);
}

.title{
    padding-top: 2.5rem;
}

.formInput{
    border-radius: 1em;
}

input:disabled{
    color: rgba(127.5,127.5,127.5,1);
}

.seinput{
    background: transparent;
    border: 0;
    border-bottom: 1px;
    outline: 0;

}

.closedEventDeleteBtn{
    position: absolute;
    top: -25%;
    left: 50%;
    transform: translate(-50%,0);
}

.courseTable{
    width: 100%;
    border-radius: 1em;
}

.courseTable .table{
    border-radius: 1em;
    padding: 0;
    margin: 0;
}

.courseTable .thead, .courseTable .tbody{
    padding-left: 0;
    padding-right: 0;
}
.courseTable .tr{
    margin-left: 0;
    margin-right: 0;
    margin-left: .6rem;
    margin-right: .6rem;
}

.courseTable .th, .courseTable .td {
    text-align: center;
    padding: 0;
    /*border: 1px solid red;*/
}

.courseTable .th{
    font-weight: 600;
}

.table{
    overflow: hidden;
}

.tbody {
    position: relative;
    padding: 0 1.5em;
}

.tbody .tr{
    border-radius: .4rem;
}

/*.tbody .tr:nth-child(odd){
    background: rgba(0,0,0,0.1);
}*/

.tbody .tr:hover{
    background-image: linear-gradient(to right bottom, red,orange,yellow);;
}

/* width */
.tbody::-webkit-scrollbar {
    width: .5em;
}
  
  /* Track */
.tbody::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 1em;
}
   
  /* Handle */
.tbody::-webkit-scrollbar-thumb {
    background: rgba(150,150,150, .5); 
    border-radius: 1em;
}
  
  /* Handle on hover */
.tbody::-webkit-scrollbar-thumb:hover {
    background: rgba(150,150,150, 1); 
}

.deleteCourse{
    position: absolute;
    right: 10%;
    top: 50%;
    transform: translate(0,-50%);
    display: none;
}

.course:hover .deleteCourse{
    display: block;
}

.addCourse {
    position: absolute;
    left: 0;
    bottom: 2.5%;
}

.addCourseBtn{
    position: absolute;
    right: 5%;
    text-align: center;
    background: none;
}

.addCourseBtn:focus{
    box-shadow: none;
    outline: none;
}

@media only screen and (max-width: 576px) {
    /* width */
    .tbody::-webkit-scrollbar {
        width: 0;
    }

    .course:hover .deleteCourse{
        display: none;
    }
}